* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #222;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
.task-container {
	background-color: #444;
	margin: 8px 0;
	padding: 15px 20px;
	display: flex;
	border-radius: 5px;
	justify-content: space-between;
	color: #eee;
	align-items: center;
}

.task-title {
	cursor: pointer;
}

.buttons-container {
	width: 30%;
	display: flex;
	justify-content: flex-end;
}

.remove-task-button,
.see-task-details-button {
	background-color: #444;
	border: none;
	font-size: 24px;
	color: chartreuse;
	margin: 0 5px;
}
.button {
	background-color: chartreuse;
	height: 40px;
	padding: 0 10px;
	border-radius: 5px;
	width: 100%;
	font-size: 14px;
	color: #444;
	font-weight: bold;
	cursor: pointer;
	border: none;
	transition: all 0.5s ease;
}

.button:hover {
	background-color: #444;
	color: chartreuse;
}
.add-task-container {
	margin: 15px 0;
	width: 100%;
	display: flex;
}

.add-task-input {
	height: 40px;
	padding: 0 10px;
	border-radius: 5px;
	border: none;
	flex: 2 1;
	background-color: #444;
	color: #eee;
	font-size: 16px;
}

.add-task-button-container {
	margin-left: 10px;
	flex: 1 1;
}

.task-details-container {
	background: #444;
	margin: 15px 0;
	padding: 15px 20px;
	display: flex;
	flex-direction: column;
	color: #eee;
	border-radius: 5px;
}

.task-details-container h2 {
	margin-bottom: 5px;
	color: chartreuse;
}

.back-button-container {
	width: 30%;
	margin-top: 10px;
}
.container{
    max-width: 370px;
    margin: 30px auto;
    overflow:auto ;
    min-width: 300px;
    border: 1px solid chartreuse;
    padding: 30px;
    border-radius: 10px;
}
